/**
 * @classDepartement
 */
export class Department {
    _id;
    _abbrev;
    constructor(id, abbrev) {
        this.id = id;
        this.abbrev = abbrev;
    }
    get id() {
        return this._id;
    }
    set id(id) {
        this._id = id;
    }
    get abbrev() {
        return this._abbrev;
    }
    set abbrev(abbrev) {
        this._abbrev = abbrev;
    }
    toString() {
        return this.abbrev;
    }
    static serialize(dept) {
        throw Error('Method not yet implemented');
    }
    static unserialize(obj) {
        return new Department(obj.id, obj.abbrev);
    }
}
