export class MarkdownDocumentation {
    _textContent;
    _paramCallCount;
    constructor(textContent, paramCallCount) {
        this._textContent = textContent;
        this._paramCallCount = paramCallCount;
    }
    get textContent() {
        return this._textContent;
    }
    get paramCallCount() {
        return this._paramCallCount;
    }
}
