/**
 * @class
 */
export class ConstrParameter {
    /**
     * Constraint parameter object types
     */
    static CONSTR_PARAMETER_TYPE = [
        "base.Week",
        "base.TrainingProgramme",
        "base.CourseType",
        "base.Module",
        "people.Tutor",
        "base.StructuralGroup",
        "base.Room",
        "base.Department"
    ];
    /**
     * Constraint parameter primitive types
     */
    static CONSTR_PARAMETER_PRIMITIVE_TYPE = [
        "PositiveSmallIntegerField",
        "CharField",
        "BooleanField",
    ];
    _name;
    _type; //Should be an enum
    _required;
    _multiple;
    _id_list;
    constructor(name, type, required, multiple, id_list) {
        this.name = name;
        this.type = type;
        this.required = required;
        this.multiple = multiple;
        this.id_list = id_list;
    }
    get name() {
        return this._name;
    }
    set name(name) {
        this._name = name;
    }
    get type() {
        return this._type;
    }
    set type(type) {
        this._type = type;
    }
    get required() {
        return this._required;
    }
    set required(required) {
        this._required = required;
    }
    get multiple() {
        return this._multiple;
    }
    set multiple(multiple) {
        this._multiple = multiple;
    }
    get id_list() {
        return this._id_list;
    }
    set id_list(id_list) {
        this._id_list = id_list;
    }
    static serialize(param) {
        throw Error('Method not yet implemented');
    }
    static unserialize(obj) {
        const id_list = [];
        obj.id_list.forEach((e) => {
            id_list.push(+e);
        });
        return new ConstrParameter(obj.name, obj.type, obj.required, obj.multiple, id_list);
    }
    static objectTypes() {
        return ConstrParameter.CONSTR_PARAMETER_TYPE;
    }
    static primitiveTypes() {
        return ConstrParameter.CONSTR_PARAMETER_PRIMITIVE_TYPE;
    }
}
