import { computed, ref } from 'vue';
/**
 * A store object consisting in a map where items are sorted by id
 * @class
 */
export class SimpleStoreMap {
    /**
     * Items sorted by id
     */
    map = ref(new Map());
    /**
     * To know if the store has already call an initialization
     * {@link initialize}
     */
    isInitialized = false;
    /**
     * Accessor for
     * {@link map}
     */
    items = computed(() => this.map.value);
    /**
     * Mutator for
     * {@link map}
     *
     * Insert a new entry
     */
    insertNew(item) {
        if (!this.map.value.has(item.id)) {
            this.map.value.set(item.id, item);
        }
        else
            throw Error('ID ' + item.id + ' alredy in the map');
    }
    /**
     * Mutator that initialize the
     * {@link map}
     *
     * Resolves if the map has already been initialized
     * Reject if {@link gatherData} reject
     */
    initialize() {
        return new Promise((resolve, reject) => {
            if (!this.isInitialized) {
                const items = this.gatherData()
                    .then((items) => {
                    items.forEach((item) => {
                        this.insertNew(item);
                    });
                    this.isInitialized = true;
                    resolve(null);
                })
                    .catch(() => {
                    this.isInitialized = false;
                    reject(null);
                });
            }
            else
                resolve(null);
        });
    }
}
