/**
 * @class
 */
export class Tutor {
    _id;
    _username;
    _first_name;
    _last_name;
    _email;
    _departments;
    constructor(id, username, first_name, last_name, email, departements) {
        this.id = id;
        this.username = username;
    }
    get id() {
        return this._id;
    }
    set id(id) {
        this._id = id;
    }
    get username() {
        return this._username;
    }
    set username(username) {
        this._username = username;
    }
    get first_name() {
        return this._first_name;
    }
    set first_name(first_name) {
        this._first_name = first_name;
    }
    get last_name() {
        return this._last_name;
    }
    set last_name(last_name) {
        this._last_name = last_name;
    }
    get email() {
        return this._email;
    }
    set email(email) {
        this._email = email;
    }
    get departments() {
        return this._departments;
    }
    set departments(departments) {
        this._departments = departments;
    }
    toString() {
        return this.username;
    }
    static serialize(tutor) {
        throw Error('Method not yet implemented');
    }
    static unserialize(obj) {
        return new Tutor(obj.id, obj.name, obj.first_name, obj.last_name, obj.email, obj.departements);
    }
}
