/**
 * @class
 */
export class ConstrParameterClass {
    _name;
    _type;
    _required;
    _multiple;
    constructor(name, type, required, multiple) {
        this.name = name;
        this.type = type;
        this.required = required;
        this.multiple = multiple;
    }
    get name() {
        return this._name;
    }
    set name(name) {
        this._name = name;
    }
    get type() {
        return this._type;
    }
    set type(type) {
        this._type = type;
    }
    get required() {
        return this._required;
    }
    set required(required) {
        this._required = required;
    }
    get multiple() {
        return this._multiple;
    }
    set multiple(multiple) {
        this._multiple = multiple;
    }
    toString() {
        return "[" + this.name + "; " + this.type + "; " + this.required + "; " + this.multiple + "]";
    }
    static serialize(param) {
        throw Error('Method not yet implemented');
    }
    static unserialize(obj) {
        return new ConstrParameterClass(obj.name, obj.type, obj.required, obj.multiple);
    }
}
