/**
 * @class
 */
export class CourseType {
    _id;
    _name;
    constructor(id, name) {
        this.id = id;
        this.name = name;
    }
    get id() {
        return this._id;
    }
    set id(id) {
        this._id = id;
    }
    get name() {
        return this._name;
    }
    set name(name) {
        this._name = name;
    }
    toString() {
        return this.name;
    }
    static serialize(dept) {
        throw Error('Method not yet implemented');
    }
    static unserialize(obj) {
        return new CourseType(obj.id, obj.name);
    }
}
