/**
 * @class
 */
export class TrainProg {
    _id;
    _abbrev;
    _name;
    constructor(id, abbrev, name) {
        this.id = id;
        this.abbrev = abbrev;
        this.name = name;
    }
    get id() {
        return this._id;
    }
    set id(id) {
        this._id = id;
    }
    get abbrev() {
        return this._abbrev;
    }
    set abbrev(abbrev) {
        this._abbrev = abbrev;
    }
    get name() {
        return this._name;
    }
    set name(name) {
        this._name = name;
    }
    toString() {
        return this.abbrev;
    }
    static serialize(TrainProg) {
        throw Error('Method not yet implemented');
    }
    static unserialize(obj) {
        return new TrainProg(obj.id, obj.abbrev, obj.name);
    }
}
